/* vdl-color using default palette 'vdl-palette-1'
  Examples of use:
  vdl-color(15);

  to specify a different color palette:
  vdl-color(4, $some-other-palette);
 */
vdl-input-container {
  display: inline-block;
  position: relative;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  line-height: normal;
  text-align: left; }
  [dir='rtl'] vdl-input-container {
    text-align: right; }

.vdl-input-wrapper {
  margin: 1rem 0;
  padding-bottom: 6px; }

.vdl-input-table {
  display: inline-table;
  flex-flow: column;
  vertical-align: bottom;
  width: 100%; }
  .vdl-input-table > * {
    display: table-cell; }

.vdl-input-infix {
  position: relative;
  width: 100%; }

.vdl-input-element {
  border: none;
  outline: none;
  padding: 0;
  width: 100%;
  margin-top: 10px;
  color: #414142;
  background: transparent;
  font: inherit; }
  .vdl-input-element::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: #a5a5a5;
    font-style: italic; }
  .vdl-input-element:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #a5a5a5;
    opacity: 1;
    font-style: italic; }
  .vdl-input-element::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #a5a5a5;
    opacity: 1;
    font-style: italic; }
  .vdl-input-element:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #a5a5a5;
    font-style: italic; }
  .vdl-end .vdl-input-element {
    text-align: right; }
    [dir='rtl'] .vdl-end .vdl-input-element {
      text-align: left; }

.vdl-input-label {
  color: #414142;
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  padding-bottom: 10px;
  font-size: 14px;
  pointer-events: none;
  z-index: 1;
  width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow-x: hidden;
  transform: translateY(0);
  transform-origin: bottom left;
  transition: transform 400ms cubic-bezier(0.25, 0.8, 0.25, 1), color 400ms cubic-bezier(0.25, 0.8, 0.25, 1), width 400ms cubic-bezier(0.25, 0.8, 0.25, 1); }
  .vdl-input-label.vdl-focused {
    color: #3a7da9; }
  .vdl-input-label.vdl-disabled {
    color: #c2c2c2; }
  .vdl-input-label.vdl-empty {
    display: block;
    cursor: text; }
  .vdl-input-label.vdl-empty.vdl-required-error.vdl-focused {
    color: #414142; }
  [dir='rtl'] .vdl-input-label {
    transform-origin: bottom right;
    left: auto;
    right: 0; }

.vdl-input-element:disabled {
  color: #a5a5a5; }

.vdl-label-required {
  color: #b1181e; }

.vdl-input-underline {
  border-color: #414142;
  position: absolute;
  height: 1px;
  width: 100%;
  margin-top: 10px;
  border-top-width: 1px;
  border-top-style: solid; }
  .vdl-input-underline.vdl-focused {
    border-color: #3a7da9;
    border-top-width: 2px; }
  .vdl-input-underline.vdl-required-error {
    border-color: #b1181e;
    border-top-width: 1px; }
  .vdl-input-underline.vdl-disabled {
    border-color: #c2c2c2; }

.vdl-hint-container {
  display: block;
  position: absolute;
  font-size: 12px;
  line-height: 12px;
  bottom: -0.5rem;
  color: #b1181e; }
  .vdl-hint-container.vdl-right {
    right: 0; }
  [dir='rtl'] .vdl-hint-container {
    right: 0;
    left: auto; }
    [dir='rtl'] .vdl-hint-container.vdl-right {
      right: auto;
      left: 0; }

.vdl-char-count {
  display: block;
  position: absolute;
  font-size: 12px;
  line-height: 12px;
  bottom: -0.5rem; }
  .vdl-char-count.vdl-right {
    right: 0; }
  [dir='rtl'] .vdl-char-count {
    right: 0;
    left: auto; }
    [dir='rtl'] .vdl-char-count.vdl-right {
      right: auto;
      left: 0; }

vdl-icon {
  height: 16px;
  width: 16px; }

.vdl-input-suffix-icons button[vdl-icon-button] {
  line-height: 0; }

.vdl-info-circle {
  color: #414142; }

.vdl-error-circle {
  color: #b1181e; }

.vdl-success-check {
  color: #1bb51e; }

.vdl-search {
  cursor: pointer;
  color: #414142; }

.vdl-clear {
  color: #414142;
  cursor: pointer; }

.vdl-password-eye {
  color: #414142;
  cursor: pointer; }

.vdl-password-eye-slash {
  color: #414142;
  cursor: pointer; }

button:focus vdl-icon {
  color: #005a99; }

.vdl-hide-clear-icon {
  visibility: hidden; }

.vdl-input-suffix-icons {
  white-space: nowrap; }

.vdl-input-suffix-icons,
.vdl-input-suffix-icons > * {
  display: inline; }

.vdl-divider {
  border-left: 1px solid #e5e5e5;
  height: 16px;
  margin-left: 10px;
  margin-right: 10px; }

.vdl-char-count-low {
  color: #f19700; }

.vdl-char-count-limit {
  color: #b1181e; }
